require('../bootstrap');

window.Vue = require('vue');

import search from '../mixins/search.js'
import favoritesMix from '../mixins/favoritesMix.js'
import notificationsMix from '../mixins/notificationsMix.js'
import articlecard from '../components/ArticleCard'
import comparisonMix from '../mixins/comparisonMix.js'

const news = new Vue({
  el: '#app',
  data: {
    articles: articles,
    categories: categories,
    query: {
      isJson: true,
      category: currentThemeSlug,
      page: 1,
    },
    append: false,
    parentCategorySlug: parentCategorySlug,
  },
  mixins: [search, favoritesMix, notificationsMix, comparisonMix],
  components: {
    articlecard
  },
  methods: {
    loadmore: function() {
      this.append = true;
      this.query.page++;
    }
  },
  watch: {
    'query.category': function() {
      this.query.page = 1;

      let newUrl = this.query.category? 
      location.protocol + '//' + location.host + '/servisy/' + this.parentCategorySlug + '/' + this.query.category 
      : location.protocol + '//' + location.host + '/servisy/' + this.parentCategorySlug
      
      
      history.pushState({
          id: 'statistics'
      }, 'Statistics', newUrl);
    },
    query: {
      handler: function(value, oldValue) {
        
        let thisUrl = this.query.category? 
        location.protocol + '//' + location.host + '/servisy/' + this.parentCategorySlug + '/' + this.query.category 
        : location.protocol + '//' + location.host + '/servisy/' + this.parentCategorySlug;
        
        var valueClone = _.cloneDeep(value);

        axios.post(thisUrl, valueClone).then(response => {
          let itterations = 1;
          
          if(this.append){
            let currentData = this.articles.data;
            this.articles = response.data.articles;
                
            currentData.reverse().forEach(item => {
              this.articles.data.unshift(item);
              
              if(itterations == currentData.length)
                this.append = false;
                
              itterations++;
            });
          }else
            this.articles = response.data.articles;
        });
      },
      deep: true
    }
  },
  created: function(){
    console.log('statistics vue created');
  }
});